import {
    AfterViewInit,
    Component,
    ElementRef,
    OnInit,
    QueryList,
    Renderer2,
    ViewChild,
    ViewChildren,
} from '@angular/core';
import { fadeAnimation } from '../../animations/fade.animation';
import { faPaperPlane } from '@fortawesome/free-solid-svg-icons';

@Component({
    selector: 'app-portfolio',
    templateUrl: './portfolio.component.html',
    styleUrls: ['./portfolio.component.scss'],
    animations: [fadeAnimation],
    host: { '[@fadeAnimation]': '' },
})
export class PortfolioComponent implements OnInit, AfterViewInit {
    @ViewChild('trackingLine')
    trackingLine: ElementRef;
    @ViewChild('introductionContainerOuter')
    introductionContainerOuter: ElementRef;
    @ViewChild('contactMeSection')
    contactMeSection: ElementRef;
    @ViewChildren('element1, element2, element3, element4, element5, element6')
    elements: QueryList<any>;
    detectedElements: ElementRef[] = [];

    introductionContainerOuterHeight = 0;
    contactMeSectionHeight = 0;
    circleTop = 0;

    constructor(private renderer: Renderer2) {
        this.renderer.listen('window', 'scroll', this.detectElms.bind(this));
    }
    // Icons
    faPaperPlane = faPaperPlane;
    ngOnInit() {}

    ngAfterViewInit() {
        this.introductionContainerOuterHeight =
            this.introductionContainerOuter.nativeElement.offsetHeight;

        this.contactMeSectionHeight =
            this.contactMeSection.nativeElement.offsetHeight;

        setTimeout(this.detectElms.bind(this));
    }

    get trackingLineLeftOffset(): string {
        if (this.trackingLine) {
            return (
                this.trackingLine.nativeElement.getBoundingClientRect().x -
                10 +
                'px'
            );
        }
    }

    get circleTopInPixel(): string {
        return this.circleTop + 'px';
    }

    sendEmail() {
        window.open('mailto:rajsaha@outlook.com');
    }

    detectElms() {
        const detectedElements: ElementRef[] = [];
        this.elements.forEach((elm, index) => {
            if (isInViewport(elm.nativeElement)) {
                detectedElements.push(elm);
            }
        });
        this.detectedElements = detectedElements;
        const firstElement = this.detectedElements[0];
        if (firstElement) {
            if (firstElement.nativeElement.id === '1' && this.trackingLine) {
                this.circleTop =
                    this.trackingLine.nativeElement.getBoundingClientRect().y;
            } else {
                this.circleTop =
                    firstElement.nativeElement.getBoundingClientRect().y;
            }
        }
    }
}

function isInViewport(element: HTMLElement): boolean {
    const rect = element.getBoundingClientRect();
    return (
        rect.top >= 0 &&
        rect.left >= 0 &&
        rect.bottom <=
            (window.innerHeight || document.documentElement.clientHeight) &&
        rect.right <=
            (window.innerWidth || document.documentElement.clientWidth)
    );
}
